
@use 'sass:color';

// ========== 配色系统 ==========
$c-bg: #0d0d0d;
$c-panel: rgba(18, 18, 18, 0.98);
$c-gold: #d4af37;
$c-gold-dim: #8a7326;
$c-red: #8a1c1c;
$c-red-light: #c54b4b;
$c-text: #e0e0e0;
$c-text-sub: #888;
$c-green: #4caf50;
$c-purple: #9c27b0;
$c-blue: #4fc3f7;
$c-cyan: #4fc3f7; // 待判定状态颜色
$c-orange: #ff9800;
$font-serif: 'Noto Serif SC', 'Songti SC', 'STSong', serif;
$font-sans: 'Roboto', sans-serif;

// ========== 容器 ==========
.mystic-container {
  position: relative;
  width: 100%;
  font-family: $font-sans;
  color: $c-text;
  background: $c-bg;
  overflow: hidden;
}

// ========== 背景纹理 ==========
.bg-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    // 暗红光晕
    radial-gradient(ellipse at 20% 0%, rgba($c-red, 0.12), transparent 50%),
    radial-gradient(ellipse at 80% 100%, rgba($c-red, 0.1), transparent 50%),
    // 金色点缀
    radial-gradient(circle at 50% 50%, rgba($c-gold, 0.04), transparent 60%),
    // 织物暗纹 - 更明显的交叉纹理
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 1px,
      rgba(255, 255, 255, 0.025) 1px,
      rgba(255, 255, 255, 0.025) 2px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 1px,
      rgba(255, 255, 255, 0.025) 1px,
      rgba(255, 255, 255, 0.025) 2px
    ),
    // 额外的大间距暗纹
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent 20px,
      rgba($c-gold, 0.015) 20px,
      rgba($c-gold, 0.015) 21px
    ),
    repeating-linear-gradient(
      0deg,
      transparent,
      transparent 20px,
      rgba($c-gold, 0.015) 20px,
      rgba($c-gold, 0.015) 21px
    );
  z-index: 0;
}

// ========== 主面板 ==========
.main-panel {
  position: relative;
  z-index: 1;
  padding: 20px 24px;
  background: $c-panel;
  border: 1px solid rgba($c-gold, 0.15);
  backdrop-filter: blur(5px);
}

// ========== 装饰线 ==========
.panel-decor {
  position: absolute;
  left: 15px;
  right: 15px;
  height: 1px;
  background: linear-gradient(90deg, transparent, $c-gold, transparent);
  opacity: 0.4;

  &.top { top: 8px; }
  &.bottom { bottom: 8px; }
}

// ========== 头部 ==========
.header {
  text-align: center;
  margin-bottom: 18px;

  .world-info {
    font-family: $font-serif;
    color: $c-gold;
    font-size: 13px;
    letter-spacing: 1px;

    .info-item {
      .icon {
        margin-right: 4px;
        font-style: normal;
      }

      // 星期显示样式
      &.weekday {
        margin-left: 6px;
        padding: 2px 8px;
        font-size: 11px;
        border-radius: 3px;
        font-weight: bold;

        // 周末（苏文全天在家）
        &.weekend {
          background: rgba($c-red, 0.15);
          color: $c-red-light;
          border: 1px solid rgba($c-red, 0.3);
        }

        // 上午班工作日（周一、三、五）
        &.morning-shift {
          background: rgba($c-gold, 0.1);
          color: $c-gold;
          border: 1px solid rgba($c-gold, 0.2);
        }

        // 下午班工作日（周二、四）
        &.afternoon-shift {
          background: rgba($c-blue, 0.1);
          color: $c-blue;
          border: 1px solid rgba($c-blue, 0.2);
        }
      }
    }

    .divider {
      margin: 0 10px;
      font-size: 8px;
      color: $c-gold-dim;
    }
  }

  .environment {
    font-size: 11px;
    color: $c-text-sub;
    margin-top: 4px;
    font-style: italic;
  }
}

// ========== 角色标签 ==========
.char-tabs {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba($c-gold, 0.1);
  padding-bottom: 15px;

  .tab-item {
    position: relative;
    padding: 8px 24px;
    cursor: pointer;
    text-align: center;
    transition: all 0.3s;
    border: 1px solid transparent;

    .char-name {
      display: block;
      font-size: 18px;
      font-family: $font-serif;
      font-weight: bold;
      color: $c-text-sub;
      transition: color 0.3s;
    }

    .char-role {
      font-size: 10px;
      color: rgba($c-text-sub, 0.5);
      letter-spacing: 2px;
    }

    &:hover .char-name {
      color: $c-text;
    }

    &.active {
      border: 1px solid rgba($c-gold, 0.3);
      background: rgba($c-gold, 0.05);

      .char-name {
        color: $c-gold;
        text-shadow: 0 0 10px rgba($c-gold, 0.3);
      }

      .char-role {
        color: $c-gold-dim;
      }

      &::after {
        content: '';
        position: absolute;
        bottom: -16px;
        left: 50%;
        width: 6px;
        height: 6px;
        background: $c-gold;
        transform: translateX(-50%) rotate(45deg);
        box-shadow: 0 0 5px $c-gold;
      }
    }
  }
}

// ========== 念头植入区 ==========
.implant-control {
  background: rgba(0, 0, 0, 0.3);
  padding: 15px;
  border: 1px solid rgba($c-gold, 0.12);
  margin-bottom: 20px;

  .control-header {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin-bottom: 12px;

    .title {
      color: $c-gold;
      font-size: 12px;
      letter-spacing: 3px;
    }

    .decor-line {
      height: 1px;
      width: 40px;
      background: $c-gold-dim;
      opacity: 0.5;
    }
  }

  .input-group {
    display: flex;
    flex-direction: column;
    gap: 8px;

    .target-hint {
      display: flex;
      align-items: center;
      justify-content: center;

      .hint-text {
        font-size: 12px;
        color: $c-text-sub;

        strong {
          color: $c-gold;
          font-weight: bold;
        }
      }
    }

    .input-wrapper {
      flex: 1;
      display: flex;
      position: relative;

      input {
        flex: 1;
        height: 34px;
        background: rgba(0, 0, 0, 0.4);
        border: 1px solid rgba($c-text-sub, 0.3);
        border-right: none;
        color: $c-text;
        padding: 0 50px 0 12px;
        font-family: $font-serif;
        outline: none;

        &::placeholder {
          color: rgba($c-text-sub, 0.6);
        }

        &:focus {
          border-color: $c-gold;
          background: rgba(0, 0, 0, 0.6);
        }
      }

      .char-count {
        position: absolute;
        right: 70px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 11px;
        color: rgba($c-text-sub, 0.6);
        pointer-events: none;

        &.at-limit {
          color: $c-gold;
        }
      }

      .submit-btn {
        background: linear-gradient(135deg, $c-red, color.adjust($c-red, $lightness: -10%));
        border: 1px solid $c-red;
        color: #ddd;
        padding: 0 20px;
        cursor: pointer;
        transition: all 0.3s;

        &:hover:not(:disabled) {
          filter: brightness(1.2);
          box-shadow: 0 0 10px rgba($c-red, 0.4);
        }

        &:disabled {
          filter: grayscale(1);
          cursor: not-allowed;
          opacity: 0.5;
        }
      }
    }
  }
}

// ========== 状态展示区 ==========
.status-display {
  display: flex;
  gap: 24px;
  min-height: 300px;

  .col-left {
    flex: 0 0 180px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  .col-right {
    flex: 1;
    overflow-y: auto;
    padding-right: 8px;
    max-height: 450px;

    &::-webkit-scrollbar {
      width: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background: $c-gold-dim;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-track {
      background: transparent;
    }
  }
}

// ========== 境界圆环 ==========
.stage-card {
  text-align: center;

  .stage-ring {
    position: relative;
    width: 110px;
    margin: 0 auto;

    .circular-chart {
      display: block;
      margin: 0 auto;
      max-width: 100%;
    }

    .circle-bg {
      fill: none;
      stroke: rgba($c-text-sub, 0.15);
      stroke-width: 2.5;
    }

    .circle {
      fill: none;
      stroke-width: 2.5;
      stroke-linecap: round;
      stroke: $c-gold;
      filter: drop-shadow(0 0 3px $c-gold);
      transition: stroke-dasharray 0.5s ease;
    }

    .stage-text {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      align-items: center;

      .label {
        font-size: 10px;
        color: $c-text-sub;
      }

      .value {
        font-size: 14px;
        color: $c-text;
        font-family: $font-serif;
        font-weight: bold;
      }
    }
  }
}

// ========== 数值组 ==========
.stats-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.stat-item {
  .stat-header {
    display: flex;
    justify-content: space-between;
    font-size: 11px;
    color: $c-text-sub;
    margin-bottom: 3px;

    .name {
      font-size: 10px;
    }

    .num {
      color: $c-text;
      font-weight: bold;
    }
  }

  .progress-track {
    height: 5px;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 2px;

    .progress-bar {
      height: 100%;
      border-radius: 2px;
      transition: width 0.5s;

      &.stat-moral { background: linear-gradient(90deg, $c-green, color.adjust($c-green, $lightness: -10%)); }
      &.stat-desire { background: linear-gradient(90deg, $c-red, color.adjust($c-red, $lightness: 10%)); }
      &.stat-husband { background: linear-gradient(90deg, $c-blue, color.adjust($c-blue, $lightness: -15%)); }
      &.stat-corruption { background: linear-gradient(90deg, $c-purple, color.adjust($c-purple, $lightness: 10%)); }
    }
  }
}

// ========== 苏文卡片 ==========
.suwen-card {
  border: 1px solid rgba($c-text-sub, 0.2);
  padding: 10px;
  background: rgba(0, 0, 0, 0.25);
  overflow: hidden;

  .suwen-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;

    .name {
      font-size: 12px;
      color: $c-text;
      font-weight: bold;
    }

    .status-tag {
      font-size: 10px;
      padding: 2px 6px;
      background: rgba($c-text-sub, 0.2);
      color: $c-text-sub;

      // 安眠药沉睡状态 - 红色高亮
      &.sleeping-pill {
        background: rgba($c-red, 0.3);
        color: $c-red;
        font-weight: bold;
        animation: sleeping-pulse 2s ease-in-out infinite;
      }

      // 住院状态（终极隐藏模式）- 金色高亮
      &.hospitalized {
        background: rgba(255, 215, 0, 0.4);
        color: #ffd700;
        font-weight: bold;
        animation: hospitalized-pulse 2s ease-in-out infinite;
        text-shadow: 0 0 4px rgba(255, 215, 0, 0.6);
      }
    }
  }

  // 安眠药沉睡动画
  @keyframes sleeping-pulse {
    0%, 100% {
      opacity: 1;
      box-shadow: 0 0 4px rgba($c-red, 0.5);
    }
    50% {
      opacity: 0.8;
      box-shadow: 0 0 8px rgba($c-red, 0.8);
    }
  }

  // 住院状态动画（终极隐藏模式）
  @keyframes hospitalized-pulse {
    0%, 100% {
      opacity: 1;
      box-shadow: 0 0 6px rgba(255, 215, 0, 0.6);
    }
    50% {
      opacity: 0.9;
      box-shadow: 0 0 12px rgba(255, 215, 0, 0.9);
    }
  }

  .suspicion-row {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 10px;
    color: $c-text-sub;
    margin-bottom: 6px;

    .label { flex: 0 0 28px; }

    .suspicion-track {
      flex: 1;
      height: 5px;
      background: rgba(255, 255, 255, 0.08);
      border-radius: 2px;
      position: relative;
      overflow: hidden;

      .suspicion-fill {
        height: 100%;
        background: $c-orange;
        border-radius: 2px;
        transition: width 0.3s;

        &.danger {
          background: $c-red;
        }
      }

      // 冻结状态
      &.frozen {
        .suspicion-fill {
          background: $c-blue;
          animation: freeze-pulse 2s ease-in-out infinite;
        }
      }

      .freeze-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba($c-blue, 0.3), transparent);
        animation: freeze-shimmer 3s linear infinite;
      }
    }

    .val {
      flex: 0 0 20px;
      text-align: right;
      color: $c-text;
    }

    .freeze-icon {
      font-size: 12px;
      animation: freeze-bounce 1s ease-in-out infinite;
    }
  }

  .freeze-info {
    background: rgba($c-blue, 0.1);
    border: 1px solid rgba($c-blue, 0.2);
    padding: 6px 8px;
    margin-bottom: 6px;
    font-size: 10px;

    .freeze-reason {
      color: $c-blue;
      margin-bottom: 2px;
    }

    .freeze-time {
      color: $c-text-sub;
      font-size: 9px;
    }
  }

  .impression-row {
    display: flex;
    gap: 6px;
    font-size: 10px;
    overflow: hidden;

    .label {
      color: $c-text-sub;
      flex: 0 0 28px;
    }

    .impression-text {
      color: $c-text;
      font-style: italic;
      flex: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      min-width: 0; // 允许 flex 子元素收缩到小于内容宽度
    }
  }

  .impression-detail-row {
    margin-top: 2px;
    padding-left: 34px;
    font-size: 9px;
    overflow: hidden;

    .detail-text {
      color: $c-text-sub;
      font-style: italic;
      opacity: 0.8;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: block;
      max-width: 100%; // 允许填满父容器而不溢出
    }
  }

  // ========== 危险监控区域 ==========
  .danger-monitor {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px dashed rgba($c-red, 0.3);

    .monitor-header {
      display: flex;
      align-items: center;
      gap: 6px;
      margin-bottom: 10px;

      .monitor-icon {
        font-size: 12px;
      }

      .monitor-title {
        font-size: 11px;
        color: $c-red-light;
        font-weight: bold;
        letter-spacing: 1px;
      }

      .risk-badge {
        margin-left: auto;
        font-size: 9px;
        padding: 2px 6px;
        border-radius: 2px;

        &.low {
          background: rgba($c-green, 0.15);
          color: $c-green;
        }
        &.medium {
          background: rgba($c-orange, 0.15);
          color: $c-orange;
        }
        &.high {
          background: rgba($c-red, 0.2);
          color: $c-red-light;
        }
        &.critical {
          background: rgba($c-red, 0.3);
          color: #ff6b6b;
          animation: risk-pulse 1s ease-in-out infinite;
        }
      }
    }

    .monitor-item {
      margin-bottom: 8px;

      .item-header {
        display: flex;
        align-items: center;
        gap: 4px;
        font-size: 10px;
        margin-bottom: 3px;

        .item-icon {
          font-size: 10px;
        }

        .item-label {
          color: $c-text-sub;
        }

        .item-value {
          margin-left: auto;
          color: $c-text;
          font-weight: bold;
          font-size: 9px;
        }
      }

      .monitor-track {
        height: 4px;
        background: rgba(255, 255, 255, 0.08);
        border-radius: 2px;
        overflow: hidden;

        .monitor-fill {
          height: 100%;
          border-radius: 2px;
          transition: width 0.5s ease;

          &.safe {
            background: linear-gradient(90deg, $c-green, color.adjust($c-green, $lightness: -5%));
          }
          &.caution {
            background: linear-gradient(90deg, #c9b022, #a89a1c);
          }
          &.warning {
            background: linear-gradient(90deg, $c-orange, color.adjust($c-orange, $lightness: -10%));
          }
          &.danger {
            background: linear-gradient(90deg, $c-red, $c-red-light);
            animation: danger-pulse 1.5s ease-in-out infinite;
          }
          &.patrol {
            background: linear-gradient(90deg, $c-blue, color.adjust($c-blue, $lightness: -15%));
          }
        }
      }

      .item-detail {
        font-size: 9px;
        color: $c-text-sub;
        margin-top: 2px;
        text-align: right;
      }
    }

    .probability-row {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 10px;
      margin-top: 8px;
      padding-top: 6px;
      border-top: 1px dotted rgba($c-text-sub, 0.2);

      .prob-label {
        color: $c-text-sub;
      }

      .prob-value {
        font-weight: bold;

        &.none {
          color: $c-text-sub;
        }
        &.low {
          color: $c-green;
        }
        &.medium {
          color: $c-orange;
        }
        &.high {
          color: $c-red-light;
        }
      }
    }
  }

  // ========== 安全提示 ==========
  .safe-indicator {
    margin-top: 10px;
    padding: 8px;
    background: rgba($c-green, 0.08);
    border: 1px solid rgba($c-green, 0.2);
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 10px;

    .safe-icon {
      color: $c-green;
      font-weight: bold;
    }

    .safe-text {
      color: $c-green;
    }

    // 安眠药生效状态 - 特殊紫红色高亮
    &.sleeping-pill-active {
      background: rgba($c-red, 0.15);
      border: 1px solid rgba($c-red, 0.4);
      animation: sleeping-pill-glow 2s ease-in-out infinite;

      .safe-icon {
        color: $c-red;
        font-size: 14px;
      }

      .safe-text {
        color: $c-red;
        font-weight: bold;
      }
    }

    // 住院状态（终极隐藏模式）- 金色高亮
    &.hospitalized-active {
      background: rgba(255, 215, 0, 0.2);
      border: 1px solid rgba(255, 215, 0, 0.5);
      animation: hospitalized-glow 2s ease-in-out infinite;

      .safe-icon {
        color: #ffd700;
        font-size: 16px;
        text-shadow: 0 0 6px rgba(255, 215, 0, 0.8);
      }

      .safe-text {
        color: #ffd700;
        font-weight: bold;
        text-shadow: 0 0 4px rgba(255, 215, 0, 0.5);
      }
    }
  }

  // 安眠药生效发光动画
  @keyframes sleeping-pill-glow {
    0%, 100% {
      box-shadow: 0 0 5px rgba($c-red, 0.3);
    }
    50% {
      box-shadow: 0 0 15px rgba($c-red, 0.6);
    }
  }

  // 住院状态发光动画（终极隐藏模式）
  @keyframes hospitalized-glow {
    0%, 100% {
      box-shadow: 0 0 8px rgba(255, 215, 0, 0.4);
    }
    50% {
      box-shadow: 0 0 20px rgba(255, 215, 0, 0.8);
    }
  }
}

// 危险脉动动画
@keyframes danger-pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

@keyframes risk-pulse {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.8; transform: scale(1.05); }
}

// 冻结动画
@keyframes freeze-pulse {
  0%, 100% { opacity: 1; filter: brightness(1); }
  50% { opacity: 0.6; filter: brightness(1.3); }
}

@keyframes freeze-shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes freeze-bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-2px); }
}

// ========== 通用 Section 标题 ==========
.section-title {
  font-size: 11px;
  color: $c-gold-dim;
  border-bottom: 1px solid rgba($c-gold, 0.1);
  padding-bottom: 4px;
  margin-bottom: 10px;
  margin-top: 16px;
  display: flex;
  align-items: center;
  gap: 8px;

  &:first-child {
    margin-top: 0;
  }

  .influence-badge {
    font-size: 9px;
    padding: 1px 6px;
    margin-left: auto;

    &.weak { color: $c-text-sub; }
    &.moderate { color: $c-gold; background: rgba($c-gold, 0.1); }
    &.strong { color: $c-red-light; background: rgba($c-red, 0.1); }
    &.extreme { color: $c-red-light; background: rgba($c-red, 0.2); font-weight: bold; }
  }
}

// ========== 心境区 ==========
.mental-section {
  .mental-content {
    background: rgba(255, 255, 255, 0.02);
    padding: 10px 12px;
    border-left: 2px solid $c-gold;

    .emotion-row {
      font-size: 12px;
      margin-bottom: 6px;

      .label {
        color: $c-text-sub;
        margin-right: 8px;
      }

      .emotion-val {
        color: $c-gold;
      }
    }

    .thought-bubble {
      font-size: 13px;
      color: $c-text;
      font-family: $font-serif;
      line-height: 1.6;
      font-style: italic;
      margin-bottom: 8px;

      .empty-thought {
        color: $c-text-sub;
        font-size: 12px;
      }
    }

    .temperament {
      font-size: 11px;
      color: $c-text-sub;
      text-align: right;
    }
  }
}

// ========== 念头列表 ==========
.thoughts-section {
  .thought-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .thought-item {
    display: flex;
    align-items: center;
    gap: 10px;
    background: rgba(0, 0, 0, 0.3);
    padding: 10px;
    border: 1px solid rgba(255, 255, 255, 0.05);

    .thought-main {
      flex: 1;
      min-width: 0; // 防止flex子元素溢出
    }

    .thought-content {
      font-size: 12px;
      color: $c-text;
      margin-bottom: 6px;
    }

    .thought-meta {
      display: flex;
      justify-content: space-between;
      font-size: 10px;
      color: $c-text-sub;
      margin-bottom: 4px;

      .diff-tag {
        padding: 1px 5px;
        border-radius: 2px;

        &.简单 { color: $c-green; background: rgba($c-green, 0.1); }
        &.中等 { color: $c-orange; background: rgba($c-orange, 0.1); }
        &.困难 { color: $c-red-light; background: rgba($c-red, 0.1); }
        &.待定 { color: $c-cyan; background: rgba($c-cyan, 0.1); }
      }

      .pending-text {
        color: $c-cyan;
        font-style: italic;
      }
    }

    .thought-bar {
      height: 3px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 1px;
      overflow: hidden; // 防止动画溢出

      .thought-fill {
        height: 100%;
        background: $c-gold;
        border-radius: 1px;
        transition: width 0.3s;
      }

      &.pending-bar {
        .pending-fill {
          width: 100%;
          height: 100%;
          background: linear-gradient(90deg, transparent, rgba($c-cyan, 0.5), transparent);
          animation: pending-shimmer 2s linear infinite;
        }
      }
    }

    &.pending {
      border-color: rgba($c-cyan, 0.3);
    }
  }

  // 删除按钮
  .delete-thought-btn {
    flex-shrink: 0;
    width: 28px;
    height: 28px;
    padding: 0;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.3);
    color: #888;
    font-size: 14px;
    cursor: pointer;
    border-radius: 3px;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background: rgba(138, 28, 28, 0.3);
      border-color: rgba(197, 75, 75, 0.5);
      color: $c-red-light;
      transform: scale(1.05);
    }

    &:active {
      transform: scale(0.95);
    }
  }
}

@keyframes pending-shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

// ========== 习惯区 ==========
.habits-section {
  .habit-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
  }

  .habit-tag {
    font-size: 11px;
    padding: 4px 10px;
    background: rgba($c-red, 0.1);
    border: 1px solid rgba($c-red, 0.25);
    color: $c-text;
  }
}

// ========== 仪容区 ==========
.appearance-section {
  .appearance-category {
    margin-bottom: 12px;
    background: rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.05);
    padding: 10px;

    &.danger-zone {
      border-color: rgba($c-red, 0.2);
      background: rgba($c-red, 0.05);
    }

    .category-header {
      display: flex;
      align-items: center;
      gap: 8px;
      margin-bottom: 8px;

      .cat-icon {
        font-size: 14px;
      }

      .cat-name {
        font-size: 12px;
        color: $c-text;
        font-weight: bold;
      }

      .cat-tags {
        margin-left: auto;
        display: flex;
        gap: 4px;
      }

      .mini-tag {
        font-size: 9px;
        padding: 2px 6px;
        background: rgba($c-gold, 0.15);
        color: $c-gold;

        &.outline {
          background: transparent;
          border: 1px solid rgba($c-text-sub, 0.3);
          color: $c-text-sub;
        }

        &.warn {
          background: rgba($c-orange, 0.15);
          color: $c-orange;
        }
      }
    }

    .category-details {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 4px 12px;

      .detail-row {
        display: flex;
        font-size: 10px;

        .d-label {
          flex: 0 0 36px;
          color: $c-text-sub;
        }

        .d-value {
          color: $c-text;
          flex: 1;
        }

        &.highlight {
          .d-value {
            color: $c-gold;
          }
        }
      }
    }
  }

  // 身体改造内容
  .bodymod-content {
    .mod-group {
      display: flex;
      align-items: flex-start;
      gap: 6px;
      margin-bottom: 8px;
      font-size: 10px;

      &:last-child {
        margin-bottom: 0;
      }

      .mod-icon {
        font-size: 12px;
        flex: 0 0 16px;
      }

      .mod-label {
        flex: 0 0 48px;
        color: $c-text-sub;
      }

      .mod-items {
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        gap: 4px;
      }

      .mod-tag {
        padding: 2px 6px;
        background: rgba($c-red, 0.15);
        border: 1px solid rgba($c-red, 0.3);
        color: $c-text;

        &.danger {
          background: rgba($c-red, 0.25);
          color: $c-red-light;
        }

        &.temp {
          background: rgba($c-purple, 0.15);
          border-color: rgba($c-purple, 0.3);
        }
      }

      .mod-text {
        color: $c-text;
      }
    }
  }
}

// ==================== 移动端响应式适配 ====================
@media screen and (max-width: 480px) {
  // 主面板减小内边距
  .main-panel {
    padding: 12px 10px;
  }

  // 头部信息换行显示
  .header {
    margin-bottom: 12px;

    .world-info {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 4px 8px;
      font-size: 11px;

      .divider {
        display: none; // 隐藏分隔符
      }

      .info-item.weekday {
        font-size: 10px;
        padding: 1px 6px;
      }
    }
  }

  // 角色标签缩小间距
  .char-tabs {
    gap: 15px;
    margin-bottom: 15px;
    padding-bottom: 12px;

    .tab-item {
      padding: 6px 16px;

      .char-name {
        font-size: 15px;
      }

      .char-role {
        font-size: 9px;
      }
    }
  }

  // 念头植入区
  .implant-control {
    padding: 10px;
    margin-bottom: 15px;

    .control-header {
      margin-bottom: 10px;

      .title {
        font-size: 11px;
        letter-spacing: 2px;
      }

      .decor-line {
        width: 25px;
      }
    }

    .input-group {
      .target-hint .hint-text {
        font-size: 11px;
      }

      .input-wrapper {
        input {
          height: 32px;
          font-size: 12px;
          padding: 0 10px;
        }

        .submit-btn {
          padding: 0 14px;
          font-size: 12px;
        }
      }
    }
  }

  // 核心变化：状态展示区改为纵向布局
  .status-display {
    flex-direction: column;
    gap: 15px;
    min-height: auto;

    .col-left {
      flex: none;
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    .col-right {
      flex: none;
      max-height: none;
      padding-right: 0;
    }
  }

  // 境界圆环适配
  .stage-card {
    .stage-ring {
      width: 90px;

      .stage-text {
        .label {
          font-size: 9px;
        }
        .value {
          font-size: 12px;
        }
      }
    }
  }

  // 核心数值组
  .stats-group {
    gap: 8px;

    .stat-item {
      .stat-header {
        font-size: 10px;

        .name {
          font-size: 10px;
        }
      }

      .progress-track {
        height: 4px;
      }
    }
  }

  // 苏文卡片
  .suwen-card {
    padding: 8px;

    .suwen-header {
      margin-bottom: 6px;

      .name {
        font-size: 11px;
      }

      .status-tag {
        font-size: 9px;
        padding: 1px 5px;
      }
    }

    .suspicion-row {
      font-size: 9px;
      gap: 4px;

      .label {
        flex: 0 0 24px;
      }

      .suspicion-track {
        height: 4px;
      }

      .val {
        flex: 0 0 18px;
      }
    }

    .freeze-info {
      padding: 5px 6px;
      font-size: 9px;

      .freeze-time {
        font-size: 8px;
      }
    }

    .impression-row {
      font-size: 9px;

      .label {
        flex: 0 0 24px;
      }
    }

    .impression-detail-row {
      padding-left: 28px;
      font-size: 8px;
    }

    // 危险监控
    .danger-monitor {
      margin-top: 8px;
      padding-top: 8px;

      .monitor-header {
        margin-bottom: 8px;

        .monitor-icon {
          font-size: 11px;
        }

        .monitor-title {
          font-size: 10px;
        }

        .risk-badge {
          font-size: 8px;
          padding: 1px 5px;
        }
      }

      .monitor-item {
        margin-bottom: 6px;

        .item-header {
          font-size: 9px;

          .item-value {
            font-size: 8px;
          }
        }

        .monitor-track {
          height: 3px;
        }

        .item-detail {
          font-size: 8px;
        }
      }

      .probability-row {
        font-size: 9px;
        margin-top: 6px;
        padding-top: 5px;
      }
    }

    .safe-indicator {
      padding: 6px;
      font-size: 9px;
    }
  }

  // 区块标题
  .section-title {
    font-size: 10px;
    margin-top: 12px;
    margin-bottom: 8px;
    padding-bottom: 3px;

    .influence-badge {
      font-size: 8px;
      padding: 1px 4px;
    }
  }

  // 心境区
  .mental-section {
    .mental-content {
      padding: 8px 10px;

      .emotion-row {
        font-size: 11px;
        margin-bottom: 4px;
      }

      .thought-bubble {
        font-size: 12px;
        line-height: 1.5;
        margin-bottom: 6px;

        .empty-thought {
          font-size: 11px;
        }
      }

      .temperament {
        font-size: 10px;
      }
    }
  }

  // 念头列表
  .thoughts-section {
    .thought-list {
      gap: 6px;
    }

    .thought-item {
      padding: 8px;

      .thought-content {
        font-size: 11px;
        margin-bottom: 4px;
      }

      .thought-meta {
        font-size: 9px;

        .diff-tag {
          padding: 0 4px;
        }
      }

      .thought-bar {
        height: 2px;
      }
    }
  }

  // 习惯区
  .habits-section {
    .habit-tags {
      gap: 4px;
    }

    .habit-tag {
      font-size: 10px;
      padding: 3px 8px;
    }
  }

  // 仪容区
  .appearance-section {
    .appearance-category {
      margin-bottom: 10px;
      padding: 8px;

      .category-header {
        margin-bottom: 6px;

        .cat-icon {
          font-size: 12px;
        }

        .cat-name {
          font-size: 11px;
        }

        .mini-tag {
          font-size: 8px;
          padding: 1px 4px;
        }
      }

      .category-details {
        grid-template-columns: 1fr; // 单列显示
        gap: 3px;

        .detail-row {
          font-size: 9px;

          .d-label {
            flex: 0 0 32px;
          }
        }
      }
    }

    .bodymod-content {
      .mod-group {
        font-size: 9px;
        margin-bottom: 6px;

        .mod-icon {
          font-size: 10px;
          flex: 0 0 14px;
        }

        .mod-label {
          flex: 0 0 40px;
        }

        .mod-tag {
          padding: 1px 4px;
          font-size: 9px;
        }
      }
    }
  }
}

// 中等屏幕适配（平板竖屏等）
@media screen and (min-width: 481px) and (max-width: 768px) {
  .main-panel {
    padding: 15px 18px;
  }

  .status-display {
    gap: 18px;

    .col-left {
      flex: 0 0 160px;
    }
  }

  .stage-card .stage-ring {
    width: 100px;
  }

  .appearance-section .appearance-category .category-details {
    grid-template-columns: 1fr; // 中等屏幕也用单列
  }
}
